button:active {
  box-shadow: inset 2px 2px 2px #e7000d;
}
#imageBird {
  text-align: center;
  margin-bottom: -1rem;
  position: relative;
  z-index: 1;
}
#imageBird img{
  max-width: 300px;
}
#quote-box {
  max-width: 50em;
  margin: 0 auto;
  border-radius: 1rem;
  padding: 1.5em;
  border: 3px solid #fc2b31;
}
.Quote {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 1rem;
}
#text {
  flex: 0 0 100%;
  margin: 0;
  font-size: 1.5rem;
  font-weight: bold;
  text-align:center;
  color: #bb0003;
  background: #fef3bb;
  padding: 1em;
  border-radius: 1rem;
}
#author {
  flex: 0 0 auto;
  font-weight: 700;
  margin-left: auto;
}
#new-quote,#tweet-quote {
  margin: 0 1rem;
}
.buttons {
  text-align: center;
  }

button {
  padding: .5rem;
  border-radius: 1rem;
  border-style: none;
  outline: none;
  cursor: pointer;
  margin: "0 1rem";
  background-color: #fc4742;
  color: #fef3bb;
  font-weight: bold;
  box-shadow: inset -1px -1px 2px #e7000d;
}
.fa-twitter {
  color:#5bdadf;
}

